* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Urbanist";
}

ol, ul {
  list-style: none;
}

a {
  display: inline-block;
  text-decoration: none;
  transition: all 0.3s ease;
}

img {
  max-width: 100%;
  height: auto;
}

input:focus, textarea:focus {
  outline: 0;
}

@font-face {
  font-family: "Urbanist";
  src: url("../../../assets/fonts/Urbanist-Black.woff2") format("woff2"), url("../../../assets/fonts/Urbanist-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
