.site-header {
  position: absolute;
  width: 100%;
  padding: 25px 0;
}
.site-header__wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.l-container {
  max-width: 1180px;
}

.l-tablet-show {
  display: block;
}
@media screen and (min-width: 768px) {
  .l-tablet-show {
    display: none;
  }
}

.l-tablet-hide {
  display: none;
}
@media screen and (min-width: 768px) {
  .l-tablet-hide {
    display: block;
  }
}

.u-h1 {
  font-size: 40px;
}
@media screen and (min-width: 768px) {
  .u-h1 {
    font-size: 50px;
  }
}
@media screen and (min-width: 1200px) {
  .u-h1 {
    font-size: 70px;
  }
}